@font-face {
  font-family: "Museo-Sans";
  src: url("../fonts/MuseoSansCyrl-700.woff2") format("woff2"), url("../fonts/MuseoSansCyrl-700.woff") format("woff"), url("../fonts/MuseoSansCyrl-700.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
* {
  box-sizing: border-box;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.row,
.col {
  padding: 0;
  margin: 0;
  --bs-gutter-x: 0;
  --bs-gutter-y: 0;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 18px;
  color: #4f4f4f;
}

h1,
h2,
h3,
h4,
h5,
a {
  color: #333333;
  font-family: "Museo-Sans", sans-serif;
}

h1 {
  font-size: 64px;
  line-height: 72px;
}

h2 {
  font-size: 56px;
  line-height: 66px;
}

p {
  line-height: 30px;
}

button:focus {
  border: 1px solid black;
}

@media (max-width: 767px) {
  h1,
  h2 {
    line-height: 44px;
    font-size: 40px;
  }
}
.container-custom {
  max-width: 1200px;
  margin: 0 auto;
}
@media (max-width: 1199px) {
  .container-custom {
    max-width: 100%;
    margin: 0 auto;
    padding: 0 40px;
  }
}
@media (max-width: 767px) {
  .container-custom {
    max-width: 100%;
    margin: 0 auto;
    padding: 0 20px;
  }
}

.btn-base {
  padding: 0;
  background-color: #4f62f6;
  color: #ffffff;
  text-transform: uppercase;
  border-radius: 60px;
  outline: 0;
  border: 0;
  font-family: "Museo-Sans", sans-serif;
  transition: all 0.3s;
}
.btn-base_size_lg {
  width: 257px;
  height: 54px;
  font-size: 16px;
}
.btn-base_size_md {
  width: 172px;
  height: 54px;
  font-size: 14px;
}
.btn-base_size_sm {
  width: 167px;
  height: 40px;
  font-size: 14px;
}
.btn-base:hover {
  background-color: #7181fc;
}
.btn-base:active {
  background-color: #354bf4;
}
.btn-base:focus {
  outline: 1px solid #000000;
}
.btn-base:disabled {
  color: #828282;
  background-color: #e0e0e0;
}

@media (max-width: 767px) {
  .btn-primary {
    width: 100%;
  }
}
.itc-select {
  position: relative;
  width: 80px;
}

.itc-select__toggle {
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 16px;
  background-color: #ffffff;
  text-transform: uppercase;
  font-size: 15px;
  line-height: 20px;
  font-family: "Museo-Sans";
  border: 1px solid black;
  border-radius: 60px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  transition: all 0.3s;
}

.itc-select__toggle::after {
  flex-shrink: 0;
  width: 20px;
  height: 20px;
  background-image: url(../images/caret-down.svg);
  background-size: cover;
  content: "";
}

.itc-select__toggle:focus {
  outline: 1px solid black;
}

.itc-select__toggle:hover {
  background-color: rgb(235, 235, 235);
}

.itc-select_show .itc-select__toggle::after {
  transform: rotate(180deg);
}

.itc-select__dropdown {
  width: 80px;
  position: absolute;
  top: 48px;
  z-index: 2;
  display: none;
  overflow-y: auto;
  border: 1px solid black;
  border-radius: 20px;
  text-transform: uppercase;
  background-color: #ffffff;
}

.itc-select_show .itc-select__dropdown {
  display: block;
}

.itc-select_show .itc-select__backdrop {
  display: block;
}

.itc-select__options {
  font-size: 15px;
  font-family: "Museo-Sans";
  margin: 0;
  padding: 0;
  list-style: none;
}

.itc-select__option {
  transition: all 0.3s;
  padding: 6px 16px;
}

.itc-select__option_selected {
  background-color: rgb(235, 235, 235);
}

.itc-select__option:hover {
  background-color: rgb(235, 235, 235);
  cursor: pointer;
}

.card {
  flex: 0 1 483px;
  height: 100%;
  max-height: 235px;
  padding: 32px;
  border: 2px solid #6248ff;
  border-radius: 30px;
}
.card__subtitle {
  margin-bottom: 8px;
  font-size: 15px;
  color: #6248ff;
  font-family: "Museo-Sans", sans-serif;
}
.card__title {
  font-size: 24px;
  margin-bottom: 24px;
}
.card__text {
  color: #4f4f4f;
}
.card:nth-of-type(2) {
  position: relative;
  top: 170px;
}
.card:nth-of-type(4) {
  position: relative;
  top: 180px;
}
.card:nth-of-type(odd):after, .card:nth-of-type(2):before {
  width: 483px;
  content: "";
  height: 150px;
  background-color: transparent;
  border-top: 3px dashed #6248ff;
  position: absolute;
  top: 50%;
  z-index: -1;
}
.card:nth-of-type(odd):after {
  border-right: 3px dashed #6248ff;
  border-radius: 0 30px 0 0;
  right: -372px;
}
.card:nth-of-type(2):before {
  border-left: 3px dashed #6248ff;
  border-radius: 30px 0 0 0;
  left: -372px;
}

@media (max-width: 1199px) {
  .card {
    max-height: 300px;
    flex-basis: 329px;
  }
  .card:nth-of-type(2) {
    position: relative;
    top: 180px;
  }
  .card:nth-of-type(4) {
    position: relative;
    top: 190px;
  }
  .card:nth-of-type(odd):after {
    right: -198px;
  }
  .card:nth-of-type(2):before {
    left: -198px;
  }
}
@media (max-width: 767px) {
  .card {
    flex-basis: 280px;
    max-height: 307px;
  }
  .card:nth-of-type(even) {
    top: 0;
  }
  .card:nth-of-type(odd):after, .card:nth-of-type(2):before {
    display: none;
  }
}
.accordion {
  max-width: 995px;
  margin: 0 auto;
}
.accordion__item {
  position: relative;
  margin-bottom: 16px;
  padding: 34px 40px;
  border: 2px solid #6248ff;
  border-radius: 20px;
  cursor: pointer;
}
.accordion__item.active > .accordion__content {
  margin-top: 24px;
  max-height: 150px;
}
.accordion__item.active > .accordion__label:before {
  transition: all 0.3s;
  transform: rotate(45deg);
}
.accordion__label {
  font-size: 24px;
  cursor: pointer;
}
.accordion__label:before {
  content: "+";
  position: absolute;
  right: 40px;
  font-size: 30px;
  line-height: 22px;
  transition: all 0.3s;
}
.accordion__content {
  max-height: 0;
  max-width: 750px;
  overflow: hidden;
  transition: all 0.3s;
  font-size: 16px;
  line-height: 27px;
  color: #4f4f4f;
}

@media (max-width: 1199px) {
  .accordion {
    max-width: 688px;
  }
  .accordion__item.active > .accordion__content {
    max-height: 162px;
  }
  .accordion__content {
    max-width: 558px;
  }
}
@media (max-width: 767px) {
  .accordion__item {
    padding: 24px 16px;
  }
  .accordion__label {
    font-size: 18px;
  }
  .accordion__item.active > .accordion__content {
    max-height: 352px;
  }
}
.form {
  border: 2px solid #6248ff;
  border-radius: 20px;
  background-color: #ffffff;
}
.form__fieldset {
  padding: 80px;
}
.form__control {
  display: block;
  width: 100%;
  padding: 7px 16px;
  border: 1px solid #e0e0e0;
  border-radius: 3px;
  transition: all 0.3s;
}
.form__control::placeholder {
  font-size: 16px;
  color: #828282;
}
.form__control:focus {
  outline: none;
  box-shadow: none;
  color: #4f4f4f;
  border-color: #4f4f4f;
}
.form__control.success {
  border-color: #0eac00;
}
.form__control.error {
  border-color: #eb5757;
}
.form__control + .error {
  font-size: 15px;
  line-height: 16px;
  color: #eb5757;
  font-family: "Museo-Sans", sans-serif;
  position: absolute;
  bottom: -20px;
}
.form__control.textarea {
  resize: none;
}
.form .btn-base {
  display: block;
  margin: 0 auto;
  font-size: 16px;
  font-weight: 500;
}

@media (max-width: 1199px) {
  .form__fieldset {
    padding: 80px 42px;
  }
}
@media (max-width: 767px) {
  .form__fieldset {
    padding: 48px 16px;
  }
}
.header {
