  padding: 0;
}
.header .navbar-nav {
  gap: 16px;
}
.header .nav-link {
  font-size: 15px;
  font-family: "Museo-Sans", sans-serif;
  color: #333333;
  transition: all 0.3s;
}
.header .nav-link:hover {
  color: #7181fc;
}

.promo {
  background-color: rgba(98, 72, 255, 0.1);
}
.promo .box {
